export default "import * as T from 'fp-ts/Task';\nimport * as SM from '@no-day/ts-stadium';\n\nimport { stateMachine } from './state-machine';\n\nexport const control = SM.createControl(stateMachine)(T.task)({\n  SwitchOn: () => async () => () => ({\n    state: SM.tag('On'),\n  }),\n  SwitchOff: () => async () => () => ({\n    state: SM.tag('Off'),\n  }),\n});\n";