export default "import * as SM from '@no-day/ts-stadium';\n\nexport const stateMachine = SM.createStateMachine({\n  states: {\n    On: {\n      events: ['SwitchOff'],\n    },\n    Off: {\n      events: ['SwitchOn'],\n      init: true,\n    },\n  },\n\n  events: {\n    SwitchOff: {\n      toStates: ['Off'],\n    },\n    SwitchOn: {\n      toStates: ['On'],\n    },\n  },\n});\n";